* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #292524;
  color: #fafaf9;
  text-transform: uppercase;
  font-family: "sono", sans-serif;
  padding: 48px 60px 0px;
}
#root {
  max-width: 1500px;

  margin: 0 auto;
}

/* main layout */
.header {
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main {
  display: grid;
  grid-template-columns: 200px 1fr;
  gap: 48px;
  height: calc(100vh - 48px - 68px - 40px);
}
@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
  }
}

/* elements */
.logo {
  display: flex;
  align-items: center;
  gap: 16px;
}
.logo img {
  height: 68px;
  width: 68px;
}
h1 {
  font-size: 42px;
  text-transform: uppercase;
  font-family: "coiny", sans-serif;
  line-height: 1;
  margin-top: 6px;
  margin-left: 20px;
}
.category {
  margin-bottom: 16px;
}

.fact {
  font-size: 20px;
  line-height: 1.4;
  margin-bottom: 16px;
  padding: 16px 24px;
  background-color: #44403c;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
  letter-spacing: -1px;
}
@media (max-width: 1200px) {
  .fact {
    flex-direction: column;
    gap: 12px;
    align-items: end;
  }
}

.tag {
  font-size: 14px;
  text-transform: uppercase;
  font-family: "coiny", sans-serif;
  padding-top: 3px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 100px;
}
.vote-buttons {
  margin-left: auto;
  display: flex;
  flex-shrink: 0;
  gap: 8px;
}
.vote-buttons button {
  font-size: 18px;
  background-color: #78716c;
  padding: 6px 12px;
  border-radius: 100px;
  color: inherit;
  font-family: inherit;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}
.vote-buttons button:hover {
  background-color: #292524;
}
.vote-buttons button:disabled {
  background-color: #292524;
}
ul {
  list-style: none;
}

.source:link,
.source:visited {
  color: #a8a29e;
  text-decoration: none;
  margin-left: 12px;
  transition: 0.5s;
}
.source:hover,
.source:active {
  color: rgb(28, 82, 208);
}
.btn {
  font-family: "coiny", sans-serif;
  color: inherit;
  text-transform: uppercase;
  line-height: 1;
  font-size: 18px;
  padding: 16px 0 13px;
  background-image: linear-gradient(135deg, #3b82f6, #ef4444, #16a34a, #eab308);
  transition: 0.3s;
  border-radius: 100px;
  cursor: pointer;
}
.btn:hover {
  transform: scale(110%) rotate(-3deg);
}
.btn-large {
  font-size: 20px;
  padding: 20px 32px 17px;
}
.btn-all-categories {
  margin-bottom: 20px;
  width: 100%;
}
.btn-category {
  width: 100%;
  background-image: none;
}
.fact-form {
  background-color: #44403c;
  margin-bottom: 40px;
  padding: 16px 32px;
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  gap: 16px;
  border-radius: 16px;
}
.fact-form input,
.fact-form select {
  width: 250px;
  background-color: #78716c;
  border: none;
  border-radius: 100px;
  padding: 16px;
  font-size: 18px;
  color: inherit;
}
.fact-form input::placeholder {
  color: #a8a29e;
}
.fact-form input:first-child {
  flex-grow: 1;
}

.fact-form span {
  font-weight: 600;
  font-size: 18px;
  margin-right: 18px;
}
@media (max-width: 1000px) {
  body {
    padding: 32px 48px;
  }
  .fact-form {
    flex-direction: column;
    align-items: stretch;
  }
  .fact-form input,
  .fact-form select {
    width: auto;
  }
}
.hidden {
  display: none;
}
.msg {
  text-align: center;
  text-transform: uppercase;
  font-size: 40px;
  font-weight: 600;
  margin-top: 24px;
}
@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
    height: auto;
  }
}
.section {
  overflow: scroll;
  padding-bottom: 40px;
}

.section::-webkit-scrollbar {
  display: none; /* disable scroll bars for Chrome, Safari, Edge and Opera */
}

@media (max-width: 600px) {
  #root {
    padding: 24px;
  }

  .header {
    flex-direction: column;
    gap: 16px;
  }

  .fact-form {
    padding: 16px;
  }

  .fact {
    padding: 12px 16px;
  }
}
.desputed {
  color: #ef4444;
  font-weight: 600;
  margin-right: 10px;
}
